.bm-app{width:100vw;height:100vh;padding:50px}.bm-app__title{font-size:20px;line-height:1.3;font-weight:700;letter-spacing:.5px;text-transform:uppercase}.bm-app__sub-title{font-size:11px;line-height:1;font-weight:500}.bm-app__color-picker{margin-top:30px;display:flex;overflow:hidden;padding-top:10px;padding-bottom:30px}.bm-app__color-picker__widget{flex:0 0 auto;margin-right:50px}.bm-app__color-picker__values{flex:1 1 auto}.bm-app__footer{margin-top:30px;border-top:2px dashed #ddd;padding-top:30px}.bm-app__footer__description{font-size:11px;line-height:1.3;max-width:600px;opacity:.6;margin-bottom:10px}.bm-app__footer__description>a{font-weight:500;border-bottom:1px dotted #999}.bm-app__footer__made-by{margin-top:20px;font-size:12px;line-height:1.3;text-transform:uppercase}.bm-app__footer__made-by>a{font-weight:600}